import React, { Fragment } from "react";
import { Hidden } from "@mui/material";

//import DesktopSocialLinking from "../../../Container/BusinessSettings/SocialMedia/Desktop";
import DesktopWidgetIntegration from "../../../Container/BusinessSettings/WidgetIntegration/Desktop";

import Layout from "../Layout";

const SocialMediaLinking: React.FunctionComponent = () => {
  return (
    <Fragment>
      <Hidden smUp implementation="js">
        
      </Hidden>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopWidgetIntegration />
        </Layout>
      </Hidden>
    </Fragment>
  );
};

export default SocialMediaLinking;
