import { Button } from "@mui/material";

import attach from "../../../assets/icons/attach.svg";

import useStyles from "./styles/muiStyles";

const FileAttachment = (props) => {
  const classes = useStyles(),
    {
      id,
      value,
      title,
      placeholder,
      name,
      onKeyPressHandler,
      inputChangedHandler,
      removeAttachment,
      height = "",
      width = "",
      hidePreview = false,
      allowRemove = true,
      allowedTypes = "png or jpg format",
      accept = undefined,
    } = props;

  return (
    <div
      className={classes["attachment"]}
      style={{ width: width, height: height }}
    >
      {!hidePreview && value !== null && value?.length !== 0 && (
        <div
          className={`image-wrapper ${!allowRemove ? "remove-disable" : ""}`}
        >
          {accept?.includes("xls") ? (
            <div className="fileName">{value.name}</div>
          ) : (
            <img
              src={
                value && typeof value !== "string"
                  ? URL.createObjectURL(value[0])
                  : value
              }
              alt="attach"
              className="image"
            />
          )}
          {removeAttachment && (
            <Button
              type="button"
              variant="outlined"
              className="btn-remove"
              onClick={() => setTimeout(() => removeAttachment(), 550)}
            >
              {"Remove"}
            </Button>
          )}
        </div>
      )}
      <div className="attachment">
        <span className="icon">
          <img src={attach} alt="attach" />
        </span>
        <h4
          className="title"
          style={{ ...(width !== "" ? { maxWidth: width } : {}) }}
        >
          {title ?? placeholder}
        </h4>
        <p className="allowed-types">{allowedTypes}</p>
        <input
          id={id}
          className="file-input"
          defaultValue={value && value}
          accept={accept}
          type={"file"}
          name={name}
          onChange={(e) => {
            inputChangedHandler && inputChangedHandler(e);
          }}
          // required={props.required}
          onKeyDown={(e) => onKeyPressHandler && onKeyPressHandler(e)}
        />
      </div>
    </div>
  );
};

export default FileAttachment;
