// axios imports
import axios from "axios";

// backend route import
import { API, API_V2 } from "../../Backend";

// upload file
export const UploadChatFile = async (formData) => {
  try {
    const uploadRes = await axios({
      // url: 'http://localhost:5000/api/chats/upload',
      url: `${API}/chats/upload`,
      method: "POST",
      data: formData,
      headers: {
        "Content-Type": "multipart/form-data",
        "X-BusinessId": localStorage.getItem("businessId"),
        "X-Auth-Token": localStorage.getItem("token"),
      },
    });

    return {
      data: uploadRes.data,
    };
  } catch (error) {
    return {
      uploadError: error.response.data,
    };
  }
};

export const UploadFile = async (formData, directory) => {
  try {
    const res = await axios({
      url: `${API_V2}/asset/?directory=${directory}`,
      method: "POST",
      data: formData,
      headers: {
        "Content-Type": "multipart/form-data",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `bearer ${localStorage.getItem("token")}`,
      },
    });

    return await res.data;
  } catch (error) {
    return {
      message: error.response.data,
    };
  }
};

export const DeleteFile = async (url) => {
  try {
    const res = await axios({
      url: `${API_V2}/asset`,
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `Bearer ${localStorage.getItem("token")}`,
      },
      data: { url },
    });

    return await res;
  } catch (error) {
    return {
      message: error.response.data,
    };
  }
};
