import {
  withStyles,
  Input,
  TextField,
  InputProps,
  TextFieldProps,
} from "@material-ui/core";
import { FC } from "react";
import classes from "./CustomTextField.module.css";

const CustomTextField = withStyles({
  root: {
    "& .MuiFormLabel-root.Mui-error": {
      color: "#f44336 !important",
    },
    "& .MuiFormLabel-root": {
      color: "#8695AC",
    },
    // borderRadius: "8px",
    "& .MuiFilledInput-root": {
      backgroundColor: "#F2F3F7",
      borderRadius: 8,

      "&::before, &::after": {
        display: "none",
      },

      "&:not(.MuiInputBase-multiline)": {
        height: 52,
        overflow: "hidden",
      },

      "& .MuiFilledInput-input": {
        paddingRight: 14,
        paddingLeft: 14,
      },
    },
    "& .MuiFilledInput-underline:before": {
      borderBottomColor: "transparent",
    },
    "& .MuiFilledInput-underline:after": {
      borderBottomColor: "transparent",
    },
    "& .MuiOutlinedInput-root": {
      "& fieldset": {
        borderColor: "red",
      },
      "&:hover fieldset": {
        borderColor: "yellow",
      },
      "&.Mui-focused fieldset": {
        borderColor: "#8695AC",
      },
    },
  },
})(TextField);

const CustomTextFieldFilled: FC<TextFieldProps> = ({
  label,
  variant = "filled",
  ...rest
}) => {
  return (
    <CustomTextField
      label={label || "Placeholder"}
      className={classes.InputFilled}
      variant={variant}
      {...rest}
    />
  );
};

const CustomInput = withStyles({
  root: {
    backgroundColor: "#f2f3f7",
    borderRadius: 8,
    padding: "3% 4% 3% 4%",
    width: "100%",
    fontSize: 14,

    "&::before, &:after": {
      display: "none",
    },
  },
})(Input);

const CustomInputField: FC<InputProps> = (rest) => {
  return <CustomInput {...rest} />;
};

export { CustomTextFieldFilled, CustomInputField };
