import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  attachment: {
    display: "flex",
    margin: "auto",
    position: "relative",
    height: "112px",
    width: "112px",

    "& .image-wrapper": {
      backgroundColor: "#F2F3F7",
      borderRadius: "15px",
      boxShadow: "0px 0px 5px 1px rgba(0, 0, 0, 0.3)",
      display: "flex",
      justifyContent: "center",
      height: "100%",
      left: 0,
      overflow: "hidden",
      position: "absolute",
      top: 0,
      width: "100%",
      zIndex: 2,

      "&:hover": {
        "&::before": {
          content: '""',
          backgroundColor: "rgba(0, 0, 0, 0.3)",
          borderRadius: "15px",
          height: "100%",
          left: 0,
          overflow: "hidden",
          position: "absolute",
          top: 0,
          width: "100%",
          zIndex: 3,
        },

        "& .btn-remove": {
          display: "block",
        },
      },

      "&.remove-disable": {
        pointerEvents: "none",
      },

      "& .fileName": {
        alignItems: "center",
        color: "#00000080",
        display: "inline-flex",
        fontSize: "16px",
        fontWeight: 400,
        lineHeight: "19px",
      },

      "& .image": {
        height: "auto",
        maxHeight: "100%",
        maxWidth: "100%",
      },

      "& .btn-remove": {
        alignItems: "center",
        backgroundColor: "#ffffff",
        border: "none",
        color: "#12B0EE",
        cursor: "pointer",
        display: "none",
        height: "48px",
        fontSize: "16px",
        fontWeight: 700,
        lineHeight: "19px",
        position: "absolute",
        margin: 0,
        left: "50%",
        top: "50%",
        textTransform: "initial",
        transform: "translate(-50%, -50%)",
        zIndex: 5,
      },
    },

    "& .attachment": {
      alignItems: "center",
      backgroundColor: "#F2F3F7",
      borderRadius: "15px",
      display: "flex",
      height: "100%",
      justifyContent: "center",
      flexDirection: "column",
      position: "relative",
      textAlign: "center",
      width: "100%",

      "& .icon": {},

      "& .title": {
        color: "#616C7D",
        fontWeight: 500,
        fontSize: "15px",
        lineHeight: "18px",
        maxWidth: "75%",
      },

      "& .allowed-types": {
        color: "#758399",
        fontWeight: 400,
        fontSize: "10px",
        lineHeight: "12px",
        marginBottom: 0,
      },

      "& .file-input": {
        backgroundColor: "transparent",
        borderRadius: "5px",
        height: "100%",
        margin: 0,
        opacity: 0,
        padding: 0,
        position: "absolute",
        top: 0,
        width: "100%",
        zIndex: 1,
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {},
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
