import axios from "axios";
import { API_V2 } from "../Backend";
import { AuthenticatedHeaders } from "../utils/ApiHeaders";

export const createTemplate = async (data) => {
  try {
    const res = await fetch(`${API_V2}/wa-templates/create-template`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(data),
    });
    return res.json();
  } catch (err) {
    return { message: err.message };
  }
};

export const updateTemplateForm = async (templateId, data) => {
  try {
    const res = await fetch(
      `${API_V2}/wa-templates/update-template-form/${templateId}`,
      {
        method: "PUT",
        headers: AuthenticatedHeaders(),
        body: JSON.stringify(data),
      }
    );
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

