export const UploadWhatsappTemplateFile = async (formData) => {
  try {
    const res = await axios({
      url: `${API_V2}/wa-templates/upload-template-file`,
      method: "POST",
      data: formData,
      headers: {
        "Content-Type": "multipart/form-data",
        "X-BusinessId": localStorage.getItem("businessId"),
        Authorization: `bearer ${localStorage.getItem("token")}`,
      },
    });

    return await res.data;
  } catch (error) {
    return {
      message: error.response.data,
    };
  }
};



