/* eslint-disable react-hooks/exhaustive-deps */
import {
  Fragment,
  FunctionComponent,
  useEffect,
  useRef,
  useState,
} from "react";
import ReactHtmlParser from "react-html-parser";
import { Button, Link } from "@mui/material";

interface IPreviewTemplateProps {
  imageUrl?: string;
  body?: string;
  header?: any;
  data?: any;
  bodyData?: any;
  components?: any;
  buttons?: any;
  urlData?: any;
  file: any;
}

const PreviewTemplateCard: FunctionComponent<IPreviewTemplateProps> = props => {
  const {
      imageUrl,
      header,
      data,
      body,
      components,
      bodyData,
      urlData,
      file,
    } = props,
    videoRef = useRef<HTMLVideoElement>(null),
    [headerMsg, setHeaderMsg] = useState(""),
    [bodyMsg, setBodyMsg] = useState(""),
    [playVideo, setPlayVideo] = useState(false),
    [videoState, setVideoState] = useState(""),
    [footerMsg, setFooterMsg] = useState(""),
    [buttonsData, setButtonsData] = useState({
      buttons: [],
    }),
    [newUrl, setNewUrl] = useState({
      url1: "",
      url2: "",
    });

  useEffect(() => {
    const res = components.filter(e => e.type === "FOOTER");
    let text = res.length ? res[0]["text"] : "";
    setFooterMsg(text);
    const buttons = components.filter(e => e.type === "BUTTONS");
    const buttonsDatas = buttons[0];
    setButtonsData(buttonsDatas);
  }, [components]);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.addEventListener("playing", event => {
        setVideoState("playing");
      });
      videoRef.current.addEventListener("pause", event => {
        setVideoState("");
      });
      videoRef.current.addEventListener("ended", event => {
        setVideoState("");
      });
      playVideo ? videoRef.current.play() : videoRef.current.pause();
    }
  }, [playVideo]);

  useEffect(() => {
    if (data.length > 0) {
      var headerMessage = header["text"].replace(
        "{{1}}",
        data[0]["form_field"]
      );
      console.log("headerMessage", headerMessage);
      setHeaderMsg(headerMessage);
    }
  }, [data, header]);

  useEffect(() => {
    if (header != undefined) {
      var headerMessage = header["text"];
      setHeaderMsg(headerMessage);
    }
  }, [header]);

  useEffect(() => {
    let finalMessage;
    let message;
    let counter = 0;
    const bodyMag = components.filter(e => e.type === "BODY");
    let initialMsg = bodyMag[0]["text"];

    let msg = "";
    const keyExist = Object.keys(bodyData).length;
    if (keyExist > 0) {
      const output = Object.keys(bodyData).map(function (key) {
        counter++;
        if (counter === 1) {
          msg = initialMsg;
        } else {
          msg = message;
        }
        if (bodyData[key] !== "") {
          message = msg.replace(
            "{{" + counter + "}}",
            bodyData[key]["form_field"]
          );
        } else {
          message = msg.replace("{{" + counter + "}}", "{{" + counter + "}}");
        }
        return message;
      });
      setBodyMsg(output.pop());
    } else {
      setBodyMsg(initialMsg);
    }
  }, [bodyData]);

  useEffect(() => {
    let message;
    let counter = 0;
    let loop = 0;
    let newUrlData = { ...newUrl };
    const buttons = components.filter(e => e.type === "BUTTONS");
    const buttonsDatas = buttons[0];
    Object.keys(urlData).forEach(key => {
      if (buttonsDatas.buttons[loop]) {
        let actionType = buttonsDatas.buttons[loop]["type"];
        console.log("actionType", actionType);

        if (actionType === "URL") {
          console.log("actionType", actionType);
          counter++;
          let defaultUrl = buttonsDatas.buttons[loop]["url"];
          if (urlData[`buttonUrl${counter}`] != "") {
            message = defaultUrl.replace(
              "{{" + counter + "}}",
              urlData[`buttonUrl${counter}`]
            );
          } else {
            message = defaultUrl.replace(
              "{{" + counter + "}}",
              "{{" + counter + "}}"
            );
          }
          newUrlData[`url${counter}`] = message;
        }
        loop++;
      }
    });
    setNewUrl(newUrlData);
  }, [urlData]);

  return (
    <div className='preview-card'>
      {header && (
        <Fragment>
          {header["format"] === "IMAGE" && (
            <div className='media-wrapper img'>
              {imageUrl && (
                <img src={imageUrl} alt='card-img' className='image' />
              )}
            </div>
          )}
          {header["format"] === "VIDEO" && (
            <Link
              underline='none'
              component='button'
              className={`media-wrapper vdo ${videoState}`}
              onClick={() => setPlayVideo(!playVideo)}
            >
              <video
                ref={videoRef}
                className='video'
                disablePictureInPicture={true}
              >
                {imageUrl && <source src={imageUrl} type='video/mp4' />}
              </video>
            </Link>
          )}
          {header["format"] === "DOCUMENT" && (
            <Link
              underline='none'
              className={`media-wrapper doc`}
              href={imageUrl}
              target='_blank'
            >
              {imageUrl
                ? file.name
                    .split(".")
                    .pop()
                    .slice(0, 4)
                    .toUpperCase()
                : ""}
            </Link>
          )}
        </Fragment>
      )}
      {header && header["format"] === "TEXT" && (
        <div className='header'>{headerMsg}</div>
      )}
      <div className='body'>
        {ReactHtmlParser(bodyMsg.replaceAll(/(?:\r\n|\r|\n)/g, "<br />"))}
      </div>
      {footerMsg && <div className='footer'>{footerMsg}</div>}

      {buttonsData && buttonsData.buttons.length > 0 && (
        <div className='preview-actions'>
          {buttonsData.buttons.map((button, index) => (
            <Fragment>
              {button.type === "QUICK_REPLY" ? (
                <Button variant='outlined' className='btn-action text-grey'>
                  <span className='label'>{button.text}</span>
                </Button>
              ) : button.type === "PHONE_NUMBER" ? (
                <Button
                  href={`tel:${button.phone_number}`}
                  variant='outlined'
                  className='btn-action text-blue'
                >
                  <span className='label'>{button.text}</span>
                </Button>
              ) : (
                <Button
                  href={newUrl[`url${index + 1}`]}
                  variant='outlined'
                  className='btn-action text-blue'
                  target='_blank'
                  rel='noreferrer'
                >
                  <svg
                    width='12'
                    height='12'
                    viewBox='0 0 12 12'
                    fill='none'
                    xmlns='//www.w3.org/2000/svg'
                  >
                    <path
                      d='M10.5378 1.11084H7.60995C7.35479 1.11084 7.14798 1.31765 7.14798 1.57281C7.14798 1.82797 7.35479 2.03478 7.60995 2.03478H9.42251L4.99274 6.46455C4.81232 6.64498 4.81232 6.93746 4.99274 7.11788C5.08298 7.20807 5.20119 7.25319 5.3194 7.25319C5.43762 7.25319 5.55583 7.20807 5.64607 7.11788L10.0758 2.68811V4.50072C10.0758 4.75588 10.2826 4.96269 10.5378 4.96269C10.793 4.96269 10.9998 4.75588 10.9998 4.50072V1.57286C10.9998 1.31765 10.793 1.11084 10.5378 1.11084Z'
                      fill='#32C5FF'
                      stroke='#32C5FF'
                      stroke-width='0.5'
                    />
                    <path
                      d='M8.64816 6.53803C8.393 6.53803 8.18619 6.74484 8.18619 7V9.965H1.92394V3.70275H5.5C5.75516 3.70275 5.96197 3.49594 5.96197 3.24078C5.96197 2.98562 5.75516 2.77881 5.5 2.77881L1.46197 2.77881C1.20681 2.77881 1 2.98562 1 3.24078V10.427C1 10.6821 1.20681 10.8889 1.46197 10.8889H8.64816C8.90333 10.8889 9.11013 10.6821 9.11013 10.427V7C9.11013 6.74484 8.90333 6.53803 8.64816 6.53803Z'
                      fill='#32C5FF'
                      stroke='#32C5FF'
                      stroke-width='0.5'
                    />
                  </svg>
                  <span className='label'>{button.text}</span>
                </Button>
              )}
            </Fragment>
          ))}
        </div>
      )}
    </div>
  );
};

export default PreviewTemplateCard;
