import React, { Fragment, useState, useEffect, MouseEventHandler } from "react";
import { Button } from "@mui/material";
import { MenuItem } from "@material-ui/core";
import { CustomTextFieldFilled } from "../../../../Components/UI/CustomTextField/CustomTextField";
import { Facebook } from "@mui/icons-material";

// react-router-dom
import { withRouter, RouteComponentProps } from "react-router-dom";

// query string
import queryString from "query-string";

// components imports
import FaceBookLinking from "../FaceBook";

//  react-facebook-login
// import FacebookLogin from "react-facebook-login";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";

// contexts imports
import { useToastMessage } from "../../../../contexts/ToastMessageContext";

// style imports
import useStyles from "../styles/muiStyles";

// interface imports
import { ISocialMediaLinkingProps } from "../interfaces";

// API value import
import { API, API_V2, FE_REDIRECT_URL } from "../../../../Backend";

// api imports
import {
  saveFBUserData,
  getFacebookPermissions,
} from "../../../../api/SocialMediaSetup";

import { getFbAdTemplates } from "../../../../api/FacebookWidget";

import LinkNewAdForm from "../LinkNewAdForm";

const DesktopWidgetIntegration: React.FunctionComponent<ISocialMediaLinkingProps> = ({
  location,
}: RouteComponentProps) => {
  // style classes
  const classes = useStyles();

  // contexts
  const { setErrorMessage, setSuccessMessage } = useToastMessage();

  // component states
  const [newAdform, setNewAdform] = useState(null);
  const [platform, setPlatform] = useState(null);
  const [fbUserId, setFbUserId] = useState(null);
  const [adTemplates, setAdTemplates] = useState([]);
  const [loginStatus, setLoginStatus] = useState(false);

  // component effects

  useEffect(() => {
    getFbAdTemplates()
      .then(res => {
        if (!res.success) {
          setErrorMessage(res.message);
        } else {
          setAdTemplates(res.payload);
        }
      })
      .catch(err => {
        setErrorMessage(err.message);
      });
  }, []);

  useEffect(() => {
    getFacebookPermissions()
      .then(data => {
        if (data.success && data.payload.includes("leads_retrieval")) {
          setLoginStatus(true);
        }
      })
      .catch(err => {
        setErrorMessage(err.message);
      });
  }, []);

  useEffect(() => {
    if (location.search) {
      // fetch the query string
      const qs: queryString.ParsedQuery<string> = queryString.parse(
        location.search
      );

      // TODO: Change the FE URL
      const replaceURL = `${FE_REDIRECT_URL}/business/social-linking`;

      window.history.replaceState({}, null, replaceURL);
    }
    // eslint-disable-next-line
  }, [location]);

  // Event handlers

  const cancelLinking = () => {
    setPlatform(null);
  };

  // LINK FB
  const HandleFBResponse: Function = async response => {
    console.log("response", response);
    // response validation
    if (
      !("accessToken" in response) &&
      !("id" in response) &&
      !("name" in response)
    ) {
      // login error
      setErrorMessage("Problem logging in with Facebook!");
      return;
    }

    setFbUserId(response.userID);

    const fbData = {
      businessId: localStorage.getItem("businessId"),
      fb_user_id: response.id,
      short_lived_token: response.accessToken,
    };

    try {
      const res = await saveFBUserData(fbData);
      if (res.success) {
        setPlatform("facebook");
      } else {
        setErrorMessage(
          res.messages ? res.messages.errors[0].message : res.message
        );
      }
    } catch (error) {
      setErrorMessage(error.message);
    }
  };

  const newTemplateMessage = async () => {
    setNewAdform(true);
  };

  const newTemplateData = data => {
    setAdTemplates([data, ...adTemplates]);
  };

  return (
    <Fragment>
      <div className={classes["stacked-cards-container"]}>
        <div className='card-group'>
          {!loginStatus && (
            <ul className='card-list'>
              <div>
                <li className='card-list-item'>
                  <FacebookLogin
                    appId={process.env.REACT_APP_APP_ID}
                    autoLoad={false}
                    fields='name,email,picture'
                    scope='leads_retrieval,ads_read,pages_manage_ads,pages_read_engagement'
                    callback={HandleFBResponse}
                    render={renderProps => (
                      <Button
                        className='btn-facebook'
                        onClick={() => {
                          renderProps.onClick();
                        }}
                      >
                        <Facebook sx={{ color: "#ffffff" }} />
                        <span className='label'>{"Sign In with Facebook"}</span>
                      </Button>
                    )}
                  />
                </li>
              </div>
            </ul>
          )}

          <Button
            variant='contained'
            type='button'
            className='btn-action'
            onClick={newTemplateMessage}
          >
            {"Link new Ad"}
          </Button>

          {adTemplates &&
            adTemplates.map(adTemplate => (
              <div>
                <p>{adTemplate.ad_account_id}</p>
              </div>
            ))}
        </div>
      </div>

      <FaceBookLinking
        open={platform === "facebook"}
        setPlatform={setPlatform}
        onClose={cancelLinking}
        fbUserId={fbUserId}
        setFbConnect={() => setLoginStatus(true)}
      />

      {newAdform && (
        <LinkNewAdForm
          setNewAdData={data => newTemplateData(data)}
          onClose={() => setNewAdform(false)}
        />
      )}
    </Fragment>
  );
};

export default withRouter(DesktopWidgetIntegration);
