// imports
import axios from "axios";
import { AuthenticatedHeaders } from "utils/ApiHeaders";

//import { API, API_V2 } from "../../Backend";

import { API_V2 } from "Backend";

const getFbAdTemplates = async adId => {
  try {
    const res = await fetch(`${API_V2}/test-widget/fb/ad-templates`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (error) {
    return { message: error.message };
  }
};

const getFbAdAccount = async () => {
  try {
    const res = await fetch(`${API_V2}/test-widget/fb/ad-account`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (error) {
    return { message: error.message };
  }
};

const getAdsList = async adAccountId => {
  try {
    const res = await fetch(
      `${API_V2}/test-widget/fb/fetch-ads/${adAccountId}`,
      {
        method: "GET",
        headers: AuthenticatedHeaders(),
      }
    );
    return await res.json();
  } catch (error) {
    return { message: error.message };
  }
};

const getLeadFormQues = async adId => {
  try {
    const res = await fetch(`${API_V2}/test-widget/fb/ad-form/${adId}`, {
      method: "GET",
      headers: AuthenticatedHeaders(),
    });
    return await res.json();
  } catch (error) {
    return { message: error.message };
  }
};

const createFbLeadAdTemplate = async data => {
  try {
    const res = await fetch(`${API_V2}/test-widget/fb/create-template`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(data),
    });
    return await res.json();
  } catch (error) {
    return { message: error.message };
  }
};

// exports
export { getFbAdTemplates, getFbAdAccount, getAdsList, getLeadFormQues, createFbLeadAdTemplate };
